<template>
    <div>
        <Button type="primary" @click.prevent="$router.go(-1)" style="margin-left: 8px">返回</Button>
        <h2>子产品名:{{this.$route.params.productSubName}}</h2>

        <Table :columns="table.cols" :data="table.data"></Table>
        <div style="margin: 10px;overflow: hidden">
            <div style="float: right;">
                <Page
                    :total="table.total"
                    :show-sizer="true"
                    placement="top"
                    @on-page-size-change="changePageSize"
                    @on-change="changePage"
                ></Page>
            </div>
        </div>

        <Modal
            v-model="model"
            :mask-closable="false"
            :title="modelTitle"
            :loading="loadingBtn"
            @on-ok="addOrUpdate"
            @on-cancel="cancel"
        >
            <Form ref="form" :model="vo" :label-width="80" :rules="validate">
                <FormItem label="市场价" prop="marketPrice">
                    <!-- <InputNumber :min="1" v-model="vo.marketPrice" style="width:100%"></InputNumber> -->
                    <InputNumber
                        :min="0"
                        v-model="vo.marketPrice"
                        style="width:100%"
                        :formatter="value =>  `${value/100}`"
                        :parser="value => `${parseInt((value*100).toFixed(2))}`"
                    />
                </FormItem>
                <FormItem label="最低价" prop="lowPrice">
                    <!-- <InputNumber :min="1" v-model="vo.lowPrice" style="width:100%"></InputNumber> -->
                    <InputNumber
                        :min="0"
                        v-model="vo.lowPrice"
                        style="width:100%"
                        :formatter="value =>  `${value/100}`"
                        :parser="value => `${parseInt((value*100).toFixed(2))}`"
                    />
                </FormItem>
                <FormItem label="结算价" prop="settlePrice">
                    <!-- <InputNumber :min="1" v-model="vo.settlePrice" style="width:100%"></InputNumber> -->
                    <InputNumber
                        :min="0"
                        v-model="vo.settlePrice"
                        style="width:100%"
                        :formatter="value =>  `${value/100}`"
                        :parser="value => `${parseInt((value*100).toFixed(2))}`"
                    />
                </FormItem>
                <FormItem label="库存" prop="stock">
                    <InputNumber :min="1" v-model="vo.stock" style="width:100%"></InputNumber>
                </FormItem>
            </Form>
        </Modal>
    </div>
</template>
<script>
import TableDataView from '@/mixins/table-data-view';
import TableColRender from '@/mixins/table-col-render';
import ModelView from '@/mixins/model-view';
export default {
    name: 'PlanList',
    mixins: [TableColRender, TableDataView, ModelView],
    data () {
        return {
            removeUrl: '/api/product/plan/delete',
            updateUrl: '/api/product/plan/update',
            validate: {},
            vo: {
                marketPrice: null,
                lowPrice: null,
                settlePrice: null,
                stock: null
            },
            table: {
                cols: [
                    {
                        title: '市场价',
                        key: 'marketPrice',
                        render: this.renderMoney
                    },
                    {
                        title: '最低价',
                        key: 'lowPrice',
                        render: this.renderMoney
                    },
                    {
                        title: '结算价',
                        key: 'settlePrice',
                        render: this.renderMoney
                    },

                    {
                        title: '开始时间',
                        key: 'startTime',
                        render: this.renderDate
                    },
                    {
                        title: '结束时间',
                        key: 'endTime',
                        render: this.renderDate
                    },
                    {
                        title: '库存',
                        key: 'stock'
                    },
                    {
                        title: '已售',
                        key: 'saleQuantity'
                    },
                    {
                        title: '操作',
                        render: (h, params) =>
                            this.renderActions(h, params, [
                                {
                                    config: {
                                        loading: this.loadingBtn,
                                        style: { marginRight: '5px' }
                                    },
                                    click: () => this.update(params.row),
                                    text: '修改'
                                },
                                {
                                    type: 'delete',
                                    config: {
                                        loading: this.loadingBtn,
                                        style: { marginRight: '5px' }
                                    },
                                    click: () => this.remove(params.row),
                                    text: '删除'
                                }
                            ])
                    }
                ],
                url: '/api/product/plan/list',
                query: {
                    productSubId: this.$route.params.productSubId
                }
            }
        };
    },
    methods: {}
};
</script>
